import FileSystemCache from 'next/dist/server/lib/incremental-cache/file-system-cache'
import type IncrementalCache from 'next/dist/server/lib/incremental-cache/file-system-cache'
import type {
  CacheHandler,
  CacheHandlerValue
} from 'next/dist/server/lib/incremental-cache'
import { isRolledOut } from './rollout'
import { KV } from '../api/kv'
import { APINotFoundError } from '../api/api'
import { EdgeCache } from '../api'
import { promises as fs } from 'fs'
import { denormalizePagePath } from 'next/dist/shared/lib/page-path/denormalize-page-path'
import { normalizePagePath } from 'next/dist/shared/lib/page-path/normalize-page-path'

type FileSystemCacheContext = ConstructorParameters<typeof IncrementalCache>[0]
type AtlasCacheHandlerValue = Omit<CacheHandlerValue, 'value'> & {
  value: any
}
type CacheHandlerParametersSet = Parameters<CacheHandler['set']>
type CacheHandlerParametersGet = Parameters<CacheHandler['get']>
type CacheHandlerParametersRevalidateTag = Parameters<
  CacheHandler['revalidateTag']
>

/**
 * Implements the Next.js custom cache handler interface to provide a remote cache
 * on the Atlas WP Engine platform. The cache handler will fall back to reading from
 * the local disk in cases where the remote cache is unavailable
 *
 * https://nextjs.org/docs/app/building-your-application/deploying#configuring-caching
 */
export class RemoteCacheHandler {
  private readonly debug: boolean
  private readonly filesystemCache: FileSystemCache
  private readonly keyPrefix = '.atlas'
  private readonly kvStore?: KV
  private readonly edgeCache?: EdgeCache
  private readonly kvStoreRolloutPercent: number
  private readonly isBuild: boolean
  private readonly buildID: string
  private readonly previewModeId: string | undefined
  private nextBuildID: string | undefined
  private prerenderManifestPath = '.next/prerender-manifest.json' // eslint-disable-line @typescript-eslint/prefer-readonly
  private buildIDPath = '.next/BUILD_ID' // eslint-disable-line @typescript-eslint/prefer-readonly

  static minISRCacheRevalidateSeconds = 10

  constructor(ctx: FileSystemCacheContext) {
    // Initialise the default Next.js Filesystem Cache Handler so we can fall
    // back to using it to read from disk if the remote cache is unavailable
    this.filesystemCache = new FileSystemCache(ctx)

    this.debug =
      String(process.env.ATLAS_CACHE_HANDLER_DEBUG).toLowerCase() === 'true' ||
      String(process.env.HEADLESS_CACHE_HANDLER_DEBUG).toLowerCase() === 'true'

    // Are we in the Atlas build environment?
    this.isBuild =
      String(process.env.HEADLESS_METADATA_BUILD).toLowerCase() === 'true'

    this.buildID = process.env.HEADLESS_METADATA_BUILD_ID ?? ''

    if (this.isKVStoreAvailable()) {
      try {
        this.kvStore = new KV()
        this.debugLog('KV store enabled')
      } catch (error) {
        console.error(this.getErrorMessage(error))
      }
    }

    try {
      this.edgeCache = new EdgeCache()
      this.debugLog('Edge Cache enabled')
    } catch (error) {
      console.error(this.getErrorMessage(error))
    }

    // Generate the rollout percent from the env var, or default to 100%
    const defaultPercent = 100
    const percentEnv = process.env.HEADLESS_CACHE_HANDLER_ROLLOUT_PERCENT ?? ''
    const percentEnvNum = parseInt(percentEnv, 10)
    this.kvStoreRolloutPercent = isNaN(percentEnvNum)
      ? defaultPercent
      : percentEnvNum

    // Get the preview mode ID from the request headers (if present)
    const xPrerenderRevalidate =
      ctx?._requestHeaders?.['x-prerender-revalidate']
    if (Array.isArray(xPrerenderRevalidate)) {
      this.previewModeId = xPrerenderRevalidate.pop()
    } else {
      this.previewModeId = xPrerenderRevalidate
    }
  }

  async get(...args: CacheHandlerParametersGet): Promise<any> {
    const [key, ctx = {}] = args

    if (!this.useKVStore(key)) {
      this.debugLog(`GET <hint:${ctx.kindHint}> ${key} (skip remote cache)`)
      return await this.filesystemCache.get(key, ctx)
    }

    const remoteKey = this.generateKey(key)
    this.debugLog(`GET <hint:${ctx.kindHint}> ${key} ${remoteKey}`)

    try {
      const data: AtlasCacheHandlerValue | null =
        await this.kvStore?.get(remoteKey)
      return data
    } catch (error) {
      const is404: boolean = error instanceof APINotFoundError
      if (!is404) {
        console.error(this.getErrorMessage(error))
      }

      try {
        const fsData = await this.filesystemCache.get(key, ctx)

        // Value was missing from the remote cache, write the local value to remote
        if (is404 && fsData?.value != null) {
          this.debugLog(`priming remote cache with ${key}`)
          await this.set(key, fsData.value, {})
        }

        return fsData
      } catch (err) {
        console.error(this.getErrorMessage(err))
        return null
      }
    }
  }

  async set(...args: CacheHandlerParametersSet): Promise<void> {
    const [key, data, ctx] = args

    if (!this.useKVStore(key)) {
      this.debugLog(`SET <kind:${data?.kind}> ${key} (skip remote cache)`)
      await this.filesystemCache.set(...args)
      return
    }

    if (data === null) {
      this.debugLog(`SET <kind:> ${key} (skip remote cache, data is null)`)
      return
    }

    const cacheEntry: CacheHandlerValue = {
      lastModified: Date.now(),
      value: data
    }

    const remoteKey = this.generateKey(key)
    this.debugLog(`SET <kind:${data.kind}> ${key} ${remoteKey}`)

    try {
      await this.kvStore?.set(remoteKey, cacheEntry)
    } catch (error) {
      console.error(this.getErrorMessage(error))
    }

    await this.filesystemCache.set(...args)

    try {
      // if revalidate not set then it is an ODISR page
      if (data.kind === 'PAGE' && (await this.isOnDemand(ctx))) {
        const paths = await this.cacheKeyToPaths(key)
        this.debugLog(
          `ODISR for Page Router revalidated, purging paths: ${paths.join(' ')}`
        )
        await this.edgeCache?.purgeByPaths(paths)
      }
    } catch (error) {
      console.error(this.getErrorMessage(error))
    }
  }

  async revalidateTag(
    ...args: CacheHandlerParametersRevalidateTag
  ): Promise<void> {
    const [tag] = args
    this.debugLog(`Revalidate Tag: ${tag.toString()}`)
    await this.filesystemCache.revalidateTag(...args)
  }

  private generateKey(key: string): string {
    key = key.replace(/^\/+/g, '') // strip leading slash
    return `${this.keyPrefix}/${this.buildID}/next/${key}`
  }

  private getErrorMessage(error: unknown): string {
    if (error instanceof Error) return error.message
    return String(error)
  }

  private debugLog(msg: string): void {
    if (this.debug) {
      console.debug('DEBUG: Remote Cache Handler: ' + msg)
    }
  }

  /**
   * Is the KV Store available for use?
   */
  private isKVStoreAvailable(): boolean {
    if (this.isBuild) {
      return false
    }

    if (!KV.isAvailable()) {
      return false
    }

    if (this.buildID === '') {
      console.log(
        'Warning: HEADLESS_METADATA_BUILD_ID is missing, remote cache disabled'
      )
      return false
    }
    return true
  }

  /**
   * Should the KV Store be used for this key?
   */
  private useKVStore(key: string): boolean {
    if (this.kvStore === undefined) {
      return false
    }

    if (this.kvStoreRolloutPercent >= 100) {
      return true
    }

    if (this.kvStoreRolloutPercent <= 0) {
      return false
    }

    // Check if KV store is rolled out for this particular key
    return isRolledOut(key, this.kvStoreRolloutPercent)
  }

  /**
   * Takes a cache key and returns the URL paths
   * @param key cache key
   * @returns array of URL paths
   */
  private async cacheKeyToPaths(key: string): Promise<string[]> {
    if (this.nextBuildID === undefined) {
      try {
        await fs.access(this.buildIDPath)
        this.nextBuildID = (await fs.readFile(this.buildIDPath, 'utf-8')).trim()
      } catch (error) {
        console.error('BUILD_ID file not found, cannot purge data path')
        throw error
      }
    }

    const pagePath = denormalizePagePath(key)
    const dataPath = `/_next/data/${this.nextBuildID}${normalizePagePath(pagePath)}.json`

    return [pagePath, dataPath]
  }

  /**
   * Check if the cache set if being triggered on-demand
   * @param ctx CacheHandler.set context
   * @returns
   */
  private async isOnDemand(
    ctx: CacheHandlerParametersSet[2]
  ): Promise<boolean> {
    // Next.js < 13.5.1 does not pass ctx so we can't know if this is an ODISR page or not
    if (ctx === undefined) {
      return false
    }

    // Is this an ODISR page
    if (ctx.revalidate === undefined || ctx.revalidate === false) {
      return true
    }

    // Revalidation period too short, don't purge
    if (ctx.revalidate < RemoteCacheHandler.minISRCacheRevalidateSeconds) {
      return false
    }

    // Is there a valid preview mode ID set on the request?
    if (this.previewModeId !== undefined) {
      try {
        await fs.access(this.prerenderManifestPath)
        const manifest = JSON.parse(
          await fs.readFile(this.prerenderManifestPath, 'utf8')
        )
        if (this.previewModeId === manifest.preview.previewModeId) {
          return true
        }
      } catch (error) {
        console.error('Could not get preview mode ID', error)
      }
    }

    return false
  }
}
